import React from 'react'
import { Spotlight } from '../ui/spotlight'


const Hero = () => {
    return (
        <div className='pb-20 pt-36 bg-black'>
            <div>
                <Spotlight className='-top-40 -left-20 md:-left-32 md:-top-20 h-screen' fill='white' />
                <Spotlight className='top-10 left-full h-[80vh] w-[50vw] ' fill='red' />
                <Spotlight className='top-28 left-80 h-[80vh] w-[50vw] ' fill='orange' />
            </div>
            <div className="h-[50rem] w-full  dark:bg-grid-white/[0.07] bg-grid-black/[0.2] absolute top-0 left-0 flex items-center justify-center">
                {/* Radial gradient for the container to give a faded look */}
                <div className="absolute pointer-events-none inset-0 flex items-center justify-center dark:bg-black-100 bg-white [mask-image:radial-gradient(ellipse_at_center,transparent_20%,black)]" />

            </div>
            <div className="flex justify-center relative my-20 z-10">
                <div className='max-w-[89vw] md:max-w-2xl lg:max-w-[60vw] flex flex-col justify-center items-center'>
                    <h2 className='uppercase tracking-widest text-xs text-center text-blue-100 max-w-80'>
                        Creating Dynamic web magics</h2>
                  
                    <p className='text-center md:tracking-wider mb-4 text-sm md:text-lg lg:text-2xl'>Hi, I&apos;m Arifa Nadia, a Junior web Developer based in Bangladesh</p>
                   
                </div>
            </div>
        </div>
    )
}

export default Hero